	module.exports = {"body":[{"questions":[{"title":"What is a hackathon?","answer":"A hackathon is an event where students with common interests from all over the nation come together and collaborate on a project over a 36-hour period."},{"title":"Who can come?","answer":"All college students (both graduate and undergraduate) are welcome! No prior experience is necessary."},{"title":"What can I build?","answer":"If you can think it, you can build it. We encourage students to build technological projects, whether they be software or hardware."},{"title":"What if I don't have a team or idea?","answer":"Not a problem. There will be time at the beginning of the hackathon to find other students who don't have a team yet!"}]},{"questions":[{"title":"What should I bring?","answer":"Bring your ID, computer, and sleeping bag (optional). All the necessary equipment for hardware projects will be provided. We'll also have a hardware lab with a variety of devices and parts."},{"title":"How much does it cost?","answer":"Food and admission are absolutely FREE! Buses will be provided for students traveling within California."},{"title":"MLH"},{"title":"How will I get there?","answer":"We will be providing buses for Californian hackers, and case-by-case flight reimbursements for out-of-state hackers. Check your email upon acceptance for more details!"},{"title":"Still have questions?","answer":"Feel free to shoot us an email at team@slohacks.com or contact us on Facebook."}]}]}

